import React, { useState } from "react";
import styled from "styled-components";
import axios from 'axios';
import { useLocation, useNavigate } from "react-router-dom";
import { useServer } from '../../../serverContext';

function MoreModal({dateValue, selectedTime, setConfirmModal }) {

  const navigate = useNavigate();

  const {server} = useServer();
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const reservId = searchParams.get("id");
  const progName = searchParams.get("prog_name");
  const progTime = searchParams.get("prog_time");
  const remain_count = searchParams.get("remain_count");
  const total_count = searchParams.get("total_count");
  const price = searchParams.get("price");

  //     sessionStorage 
  const sessionStorageKeys = Object.keys(sessionStorage);
  const sessionStorageData = {};
  sessionStorageKeys.forEach(key => {
    sessionStorageData[key] = sessionStorage.getItem(key);
  });
  const { consent, gender, id, name, passwd, phone, std } = sessionStorageData;

  const [clickYesNo, setClickYesNo] = useState('');
  const [inputText, setInputText] = useState('');

  const goMyTree = () => {
    setConfirmModal(false)
    navigate('/myTree')
  }

  const onClickYes = async () => {
    const SERVER_URL = server+'/reservationMore1'
    await axios.post(SERVER_URL, {
      client_id: id,
      reservation_id: reservId,
      name: name,
      phone: phone,
      gender: gender,
      std: std,
      prog_name: progName,
      prog_time: progTime,
      remain_count: remain_count,
      total_count: total_count,
      note: inputText,
      reservation_date: dateValue,
      reservation_time: selectedTime,
      price: price,
      discount: price,
      reservation_status: ''
    })
      .then(res => {
        if (res.data === 1){
          alert('     .')
          return false;
        }
        setClickYesNo('yes')
        const SERVER_URL =  server+'/reservationMore'
        axios.post(SERVER_URL, {
          id: reservId,
          note: inputText,
          reservation_date: dateValue,
          reservation_time: selectedTime,
        })
      })
      .catch(error => console.log(error));
  }

  const onClickNo = () => {
    setClickYesNo('no')
  }

  const handleInputChange = (event) => {
    setInputText(event.target.value);
  }

  return (
    <Overlay>
      <ModalWrap onClick={(e) => e.stopPropagation()}>
        {clickYesNo === 'yes' ?
          <>
            <div class="reservationModal-header1"> .</div>
            <div class="reservationModal-warning">    .</div>
            <button class="reservationModal-confirm" onClick={() => goMyTree()}></button>
          </>
          :
          clickYesNo === 'no'
            ?
            <>
              <div class="reservationModal-header1"> .</div>
              <button class="reservationModal-confirm" onClick={() => setConfirmModal(false)}></button>
            </>
            :
            <>

              <div className="myTree-modal-header">
                <div className="myTree-modal-title"> </div>
              </div>
              <div className="myTree-modal-content">
                <div className="myTree-modal-inner-content">
                  <div className="myTree-modal-inner-txt"> / </div>
                  <div className="myTree-modal-inner-data">{dateValue} / {selectedTime}</div>
                </div>
                <div className="myTree-modal-inner-content">
                  <div className="myTree-modal-inner-txt"></div>
                  <div className="myTree-modal-inner-data">
                    <div className="myTree-modal-inner-view">
                      <div>{progName}</div>
                      <div className="small-txt">({progTime}min)</div>
                    </div>
                  </div>
                </div>
                <div className="myTree-modal-inner-content">
                  <div className="myTree-modal-inner-txt">
                    <div className="myTree-modal-inner-view">
                      <div></div>
                      <div className="small-txt">(/)</div>
                    </div>
                  </div>
                  <div className="myTree-modal-inner-data">{remain_count} / {total_count} </div>
                </div>
                <div class="myTree-modal-inner-content">
                  <div class="myTree-modal-inner-txt"></div>
                  <input class="reservationModal-info"
                    type="text" //   
                    value={inputText} //      
                    onChange={handleInputChange} //      
                  />
                </div>
              </div>
              <div className="myTree-modal-bottom">
                <button className="myTree-modal-btn btn-wrong" onClick={onClickNo}></button>
                <button className="myTree-modal-btn btn-right" onClick={onClickYes}></button>
              </div>
            </>
        }

      </ModalWrap>
    </Overlay>
  );
}

const Overlay = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9999;
`;

const ModalWrap = styled.div`
  width: 370px;
  height: fit-content;
  border-radius: 15px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
`;

export default MoreModal;